.navbar-profile-pic img{
    border-radius: 90px;
    height: 150px;
    width: 150px;
    display: flex;
    vertical-align: middle;
    margin: 0 auto;
    margin-top: 20px;
}
.nav-item{
    color: aliceblue;
    margin-top: 30px;
    margin-left: 25px;
}
.nav-link{
    margin-top: 20px;
    cursor:pointer;
}
.nav-link svg{
    margin-right: 20px;
}
.nav-link:hover{
   color: #f29f67;
}